import { Form, Button, ButtonGroup, Checkbox, FormControl, FormGroup, ControlLabel, HelpBlock, Grid, Row, Col, InputGroup, Panel, ListGroup, ListGroupItem, Dropdown, MenuItem, DropdownButton, PanelGroup, Glyphicon, Popover, Overlay, OverlayTrigger } from 'react-bootstrap';
import Switch from "react-bootstrap-switch"

class IndicatorPanel_RSI extends React.Component
{
    constructor(props)
    {
		super(props);
        
        this.periodRef;
        
        this.getValue = this.getValue.bind(this);
        this.validate = this.validate.bind(this);
        this.getValidationState = this.getValidationState.bind(this);
    }
    
    render()
    {
        var { onChange, onRemove, addbutton, defaultValue } = this.props;
        // const popoverHoverFocus = (<Popover id="" title="RSI">This is a description.</Popover>);
                    // <OverlayTrigger trigger={['hover', 'focus']} placement="bottom" overlay={popoverHoverFocus}>
                // <FormControl inputRef={(r)=>ref_Period=r} defaultValue="14" type="text" onChange={onChange}/>
            // </OverlayTrigger>
            
        if (!defaultValue)
            defaultValue = this.getDefaultValue();
        var dv = defaultValue[1].map(v=>v.toString());
        
        var oc = (e)=>{this.setState({value: e.target.value});if (this.validate()) onChange()}; // set state to refresh validation state
        
        return(
            <div className="panel panel-default" style={{'marginBottom': '0px'}}>
                <div className="panel-heading" style={{'backgroundColor': '#97ccff'}}>
                    RSI
                    <div className="pull-right">
                        {addbutton}
                        <Button bsSize='xs' onClick={()=>onRemove(this)}><Glyphicon glyph="remove"/></Button>
                    </div>
                </div>
                <div className="panel-body" style={{'padding': '0px'}}>
                    <InputGroup bsSize='sm' style={{'width': '100%'}}>
                        <InputGroup.Addon style={{'width': '100px'}}>
                            {'Period'}
                        </InputGroup.Addon>
                        <FormControl inputRef={(r)=>{if(!this.periodRef)this.periodRef=r}} defaultValue={dv[0]} type="text" onChange={oc} />
                    </InputGroup>
                </div>
            </div>
        );
    }
    
    setValue(pValue)
    {
        this.periodRef.value(pValue[1][0].toString());
    }
    
    getDefaultValue()
    {
        return ["RSI", [14], 100];
    }
    
    getValue()
    {
        if (!this.validate(this.periodRef))
            return;
        return ["RSI", [parseInt(this.periodRef.value)], 100];
    }
    
    validate(pComp)
    {
        if (!pComp)
            return true;
        var patt = /^[0-9]+$/
        return patt.test(pComp.value);
    }
    
    getValidationState(pComp)
    {
        if (this.validate(pComp))
            return 'success';
        else
            return 'error';
    }
}

