class TTLChartControl extends React.Component
{
    constructor(props) {
		super(props);
        
        this.refMap = {};
        
        this.state={};
        this.indicatorRowIDMap={};
        this.indicatorRefMap={};
        
		this.refresh = this.refresh.bind(this);
        
		this.componentDidMount = this.componentDidMount.bind(this);
        
		this.importState = this.importState.bind(this);
		this.exportState = this.exportState.bind(this);
		this.refreshState = this.refreshState.bind(this);
		this.setChartMethod = this.setChartMethod.bind(this);
        
		this.createAddIndicatorButton = this.createAddIndicatorButton.bind(this);
		this.getIndicators = this.getIndicators.bind(this);
		this.addIndicator = this.addIndicator.bind(this);
		this.removeIndicator = this.removeIndicator.bind(this);
		this.addIndicatorRef = this.addIndicatorRef.bind(this);
		this.getIndicatorValues = this.getIndicatorValues.bind(this);
		this.changeMainChartSeries = this.changeMainChartSeries.bind(this);
		this.changeInSubChart = this.changeInSubChart.bind(this);
        
		this.getValue = this.getValue.bind(this);
		this.createIndicatorPanel = this.createIndicatorPanel.bind(this);
    }
    
    refresh()
    {
        this.setState({
            refreshState: !this.state.refreshState
        });
    }
    componentDidMount()
    {
        if (!this.mainChartSeries) // Borrow flag
            this.changeMainChartSeries("Candle");
            // this.importState(["Candle", [["InChartVol",true],["InChartSMA",true,[60]],["InChartEMA",false,[70]],["InChartBB",true]], [["RSI", [11], 100], ["MACD", [13,5,3], 125]]]);
    }
    
    render() {
        var { chartMethod } = this.props;
        var { indicators, refreshState, controlShow} = this.state;
        this.chartMethodGlobal = chartMethod;
        var cc = this.changeInSubChart;
        var cm = (pEventKey, pEvent) => {this.changeMainChartSeries(pEventKey)};
        
        if(!indicators)
            indicators=[];
        
        var lvAddDropDown = this.createAddIndicatorButton("main");
        return (
            <div>
                <Button bsSize='sm' onClick={()=>this.setState({controlShow: !controlShow})}><Glyphicon glyph="cog"/></Button>
                <div style={{'display': (controlShow ? 'block' : 'none')}} className="TTLStockChart_C_Ctl_Disp">
                    <div className="panel panel-default" style={{'marginBottom': '0px'}} id="mainCtrl">
                        <div className="panel-heading">
                            Display
                            <div className="pull-right">
                                <DropdownButton pullRight className="btn btn-default pull-right" title="Chart Type" bsSize='xs' id="Display_MainChartSeriesType" onSelect={cm}>
                                    <MenuItem eventKey="Candle">Candle</MenuItem>
                                    <MenuItem eventKey="Line">Line</MenuItem>
                                    <MenuItem eventKey="Area">Area</MenuItem>
                                    <MenuItem eventKey="OHLC">OHLC</MenuItem>
                                </DropdownButton>
                            </div>
                        </div>
                        <div className="panel-body" style={{'padding': '0px'}}>
                            <InputGroup bsSize='sm'>
                                <InputGroup.Addon>
                                    <Switch bsSize="mini" ref={(ref)=>this.refMap["Display_InChartVol_Check"]=ref} defaultValue={false} onChange={cc}/>
                                    {' Volume'}
                                </InputGroup.Addon>
                            </InputGroup>
                            <InputGroup bsSize='sm'>
                                <InputGroup.Addon>
                                    <Switch bsSize="mini" ref={(ref)=>this.refMap["Display_SMA_Check"]=ref} defaultValue={false} onChange={cc}/>
                                    {' SMA'}
                                </InputGroup.Addon>
                                <FormControl inputRef={(ref)=>this.refMap["Display_SMA_Para"]=ref} defaultValue="10,20" type="text" onChange={cc}/>
                            </InputGroup>
                            <InputGroup bsSize='sm'>
                                <InputGroup.Addon>
                                    <Switch bsSize="mini" ref={(ref)=>this.refMap["Display_WMA_Check"]=ref} defaultValue={false} onChange={cc}/>
                                    {' WMA'}
                                </InputGroup.Addon>
                                <FormControl inputRef={(ref)=>this.refMap["Display_WMA_Para"]=ref} defaultValue="10,20" type="text" onChange={cc}/>
                            </InputGroup>
                            <InputGroup bsSize='sm'>
                                <InputGroup.Addon>
                                    <Switch bsSize="mini" ref={(ref)=>this.refMap["Display_EMA_Check"]=ref} defaultValue={false} onChange={cc}/>
                                    {' EMA'}
                                </InputGroup.Addon>
                                <FormControl inputRef={(ref)=>this.refMap["Display_EMA_Para"]=ref} defaultValue="10,20" type="text" onChange={cc}/>
                            </InputGroup>
                            <InputGroup bsSize='sm'>
                                <InputGroup.Addon>
                                    <Switch bsSize="mini" ref={(ref)=>this.refMap["Display_SAR_Check"]=ref} defaultValue={false} onChange={cc}/>
                                    {' SAR'}
                                </InputGroup.Addon>
                                <FormControl inputRef={(ref)=>this.refMap["Display_SAR_Para"]=ref} defaultValue="0.02,0.2" type="text" onChange={cc}/>
                            </InputGroup>
                            <InputGroup bsSize='sm'>
                                <InputGroup.Addon>
                                    <Switch bsSize="mini" ref={(ref)=>this.refMap["Display_BB_Check"]=ref} defaultValue={false} onChange={cc}/>
                                    {' Bollinger (20, 2)'}
                                </InputGroup.Addon>
                            </InputGroup>
                        </div>
                        <div className="panel-heading">
                            Indicators
                            <div className="pull-right">
                                {lvAddDropDown}
                            </div>
                        </div>
                        <div className="panel-body" style={{'padding': '0px'}}>
                            {indicators}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
    
    setChartMethod(pChartMethod)
    {
        this.chartMethodGlobal = pChartMethod;
    }
    
    importState(pState)
    {
        if (pState[0])
        {
            this.changeMainChartSeries(pState[0])
        }
        if (pState[1])
        {
            pState[1].forEach((pInChartState)=>
            {
                if (pInChartState[0] == "InChartVol")
                {
                    this.setValue("Display_InChartVol_Check", pInChartState[1]);
                }
                else if (pInChartState[0] == "InChartSMA")
                {
                    this.setValue("Display_SMA_Check", pInChartState[1]);
                    this.setValue("Display_SMA_Para", pInChartState[2]);
                }
                else if (pInChartState[0] == "InChartWMA")
                {
                    this.setValue("Display_WMA_Check", pInChartState[1]);
                    this.setValue("Display_WMA_Para", pInChartState[2]);
                }
                else if (pInChartState[0] == "InChartEMA")
                {
                    this.setValue("Display_EMA_Check", pInChartState[1]);
                    this.setValue("Display_EMA_Para", pInChartState[2]);
                }
                else if (pInChartState[0] == "InChartSAR")
                {
                    this.setValue("Display_SAR_Check", pInChartState[1]);
                    this.setValue("Display_SAR_Para", pInChartState[2]);
                }
                else if (pInChartState[0] == "InChartBB")
                {
                    this.setValue("Display_BB_Check", pInChartState[1]);
                }
            });
        }
        if (pState[2])
        {
            this.removeAllIndicator();
            pState[2].forEach((pInChartState)=>
            {
                this.addIndicator(pInChartState[0], "last", pInChartState);
            });
        }
    }
    
    exportState()
    {
        var lvInChartList = [];
        var lvSubChartList = [];
        
        lvInChartList.push(["InChartVol", this.getValue("Display_InChartVol_Check")]);
        lvInChartList.push(["InChartSMA", this.getValue("Display_SMA_Check"), this.getValue("Display_SMA_Para")]);
        lvInChartList.push(["InChartWMA", this.getValue("Display_WMA_Check"), this.getValue("Display_WMA_Para")]);
        lvInChartList.push(["InChartEMA", this.getValue("Display_EMA_Check"), this.getValue("Display_EMA_Para")]);
        lvInChartList.push(["InChartSAR", this.getValue("Display_SAR_Check"), this.getValue("Display_SAR_Para")]);
        lvInChartList.push(["InChartBB", this.getValue("Display_BB_Check")]);
    
        this.getIndicatorValues().forEach((values)=>
        {
            if (values.value === undefined)
            {
                lvSubChartList.push(values.defaultValue);
            }
            else
            {
                lvSubChartList.push(values.value);
            }
        });
        
        return [this.mainChartSeries, lvInChartList, lvSubChartList];
    }
    
    refreshState()
    {
        this.changeMainChartSeries(this.mainChartSeries);
        //refresh
        this.changeInSubChart();
    }
    
    createAddIndicatorButton(pIndicatorRowID)
    {
        var lvID = "Add_"+pIndicatorRowID;
        var onSelect = (pEventKey, pEvent) => {this.addIndicator(pEventKey, pIndicatorRowID)};
        return (<Dropdown pullRight={true} id={lvID}>
                    <Dropdown.Toggle noCaret bsSize='xs'>
                        <Glyphicon glyph="plus" />
                    </Dropdown.Toggle>
                    <Dropdown.Menu onSelect={onSelect}>
                        <MenuItem eventKey="RSI">RSI</MenuItem>
                        <MenuItem eventKey="MACD">MACD</MenuItem>
                        <MenuItem eventKey="STC">STC</MenuItem>
                    </Dropdown.Menu>
                </Dropdown>)
    }
    
    getIndicators()
    {
        return this.state.indicators;
    }
    
    addIndicator(pIndicatorName, pIndicatorRowID, pDefaultValue)
    {
        var indicators = this.state.indicators;
        if(!indicators) indicators=[];
        
        var lvIndicatorRowID = "IndicatorRow_"+new Date().getTime();
        
        var panel = this.createIndicatorPanel(pIndicatorName, lvIndicatorRowID, pDefaultValue);
        
        this.indicatorRowIDMap[lvIndicatorRowID]=panel;
        var prevPanel = this.indicatorRowIDMap[pIndicatorRowID];
        
        var index;
        if (pIndicatorRowID == "main")
        {
            index = 0;
        }
        else if (pIndicatorRowID == "last")
        {
            index = indicators.length;
        }
        else
        {
            for (var i=0;i<indicators.length;i++)
                if (indicators[i] == prevPanel)
                {
                    index = i+1;
                    break;
                }
        }
        indicators.splice(index,0,panel);
        
        this.setState({
            indicators: indicators
        })
    }
    
    removeIndicator(pIndicatorRowID)
    {
        var indicators = this.state.indicators;
        if(!indicators) indicators=[];
        
        var panel = this.indicatorRowIDMap[pIndicatorRowID]
        delete this.indicatorRowIDMap[pIndicatorRowID]
        delete this.indicatorRefMap[pIndicatorRowID]
        
        for(var i = indicators.length - 1; i >= 0; i--)
            if(indicators[i] === panel)
            {
                indicators.splice(i, 1);
                break;
            }
        
        this.setState({
            indicators: indicators
        })
        
        //refresh
        this.changeInSubChart();
    }
    
    removeAllIndicator()
    {
        var indicators = this.state.indicators;
        if(indicators)
            for (var i=0;i<indicators.length;i++)
            {
                delete this.indicatorRowIDMap[indicators[i].key]
                delete this.indicatorRefMap[indicators[i].key]
            }
            
        this.setState({
            indicators: []
        })
        
        //refresh
        this.changeInSubChart();
    }
    
    addIndicatorRef(pIndicatorRowID, pRef)
    {
        if (this.indicatorRowIDMap[pIndicatorRowID])
        {
            this.indicatorRefMap[pIndicatorRowID] = pRef;
            //refresh
            this.changeInSubChart();
        }
    }
    
    getIndicatorValues()
    {
        var lvSubChartListPara = [];
        
        var indicators = this.state.indicators;
        if (indicators)
            for (var i=0;i<indicators.length;i++)
                lvSubChartListPara.push({value: this.indicatorRefMap[indicators[i].key].getValue(), defaultValue: this.indicatorRefMap[indicators[i].key].getDefaultValue()})
        
        // for (var key in this.indicatorRefMap)
            // if (this.indicatorRefMap.hasOwnProperty(key))
                // lvSubChartListPara.push(this.indicatorRefMap[key]());
        return lvSubChartListPara;
    }
    
    setValue(pID, pValue)
    {
        if (pID == "Display_SMA_Para"
            || pID == "Display_WMA_Para"
            || pID == "Display_EMA_Para"
            || pID == "Display_SAR_Para")
        {
            this.refMap[pID].value = pValue.toString();
        }
        else if(pID == "Display_InChartVol_Check"
            || pID == "Display_SMA_Check"
            || pID == "Display_WMA_Check"
            || pID == "Display_EMA_Check"
            || pID == "Display_SAR_Check"
            || pID == "Display_BB_Check")
        {
            this.refMap[pID].value(pValue);
        }
    }
    
    getValue(pID)
    {
        var value;
        if (pID == "Display_SMA_Para"
            || pID == "Display_WMA_Para"
            || pID == "Display_EMA_Para")
        {
            value = this.refMap[pID].value.split(",").map(pStr=>parseInt(pStr));
        }
        else if (pID == "Display_SAR_Para")
        {
            value = this.refMap[pID].value.split(",").map(pStr=>parseFloat(pStr));
        }
        else if(pID == "Display_InChartVol_Check"
            || pID == "Display_SMA_Check"
            || pID == "Display_WMA_Check"
            || pID == "Display_EMA_Check"
            || pID == "Display_SAR_Check"
            || pID == "Display_BB_Check")
        {
            value = this.refMap[pID].value()
        }
        return value;
    }
    
    changeMainChartSeries(pSeries)
    {
        this.mainChartSeries = pSeries;
        if (this.chartMethodGlobal && this.chartMethodGlobal.chartExist)
            this.chartMethodGlobal.setMainChartSeries(pSeries);
    }

    changeInSubChart()
    {
        var lvInChartVol = false;
        var lvInChartList = [];
        var lvSubChartList = [];
        
        var state = this.exportState();
        state[1].forEach((pInChartState)=>
        {
            if (pInChartState[0] == "InChartVol")
                lvInChartVol = pInChartState[1];
            else
            {
                if (pInChartState[1])
                    lvInChartList.push([pInChartState[0], pInChartState[2]]);
            }
        });
        state[2].forEach((pInChartState)=>
        {
            lvSubChartList.push(pInChartState);
        });
    
        if (this.chartMethodGlobal && this.chartMethodGlobal.chartExist)
            this.chartMethodGlobal.setSubCharts(lvInChartVol, lvInChartList, lvSubChartList);
    }

    createIndicatorPanel(pName, pPanelID, pDefaultValue)
    {
        var onChange = ()=>this.changeInSubChart();
        var onRemove = ()=>this.removeIndicator(pPanelID);
        var refFunc = (ref) => this.addIndicatorRef(pPanelID, ref);
        var addIndicatorButton = this.createAddIndicatorButton(pPanelID);
        if (pName == "RSI")
        {
            return (<IndicatorPanel_RSI ref={refFunc} key={pPanelID} onChange={onChange} onRemove={onRemove} addbutton={addIndicatorButton} defaultValue={pDefaultValue}/>);
        }
        else if (pName == "MACD")
        {
            return (<IndicatorPanel_MACD ref={refFunc} key={pPanelID} onChange={onChange} onRemove={onRemove} addbutton={addIndicatorButton} defaultValue={pDefaultValue}/>);
        }
        else if (pName == "STC")
        {
            return (<IndicatorPanel_STC ref={refFunc} key={pPanelID} onChange={onChange} onRemove={onRemove} addbutton={addIndicatorButton} defaultValue={pDefaultValue}/>);
        }
    }
}

TTLChartControl.propTypes = {
	chartMethod: PropTypes.object.isRequired
};

TTLChartControl.defaultProps = {
};

export default TTLChartControl


