/*global define*/
"use strict";

// react element
import React from "react";
import PropTypes from 'prop-types'; // ES6

// react-stockcharts
import { ChartCanvas, Chart } from "react-stockcharts";
import { discontinuousTimeScaleProvider } from "react-stockcharts/lib/scale";
import { XAxis, YAxis } from "react-stockcharts/lib/axes";
import { AreaSeries } from "react-stockcharts/lib/series";
import { first, last, mousePosition, getClosestItemIndexes } from "react-stockcharts/lib/utils";

// TTL element
import MovingWindow from "./MovingWindow"

class TTLTimeLineChart extends React.Component {
	constructor(props) {
		super(props);
        
		this.setData = this.setData.bind(this);
		this.handleMouseDown = this.handleMouseDown.bind(this);
		this.handleMouseUp = this.handleMouseUp.bind(this);
		this.handleMouseLeave = this.handleMouseLeave.bind(this);
		this.handleMouseMove = this.handleMouseMove.bind(this);
		this.refreshMovingWindow = this.refreshMovingWindow.bind(this);
        
        this.state = {
            data: this.props.data,
        }
        
        var isMob = false;
        if (isMob)
        {
            this.chartLayout = {
                chartCanvas_margin_right: 80,
                axis_fontSize: 22,
                tooltip_fontSize: 16,
                subchart_padding: 30,
                tooptip_width: 90,
            };
        }
        else
        {
            this.chartLayout = {
                chartCanvas_margin_right: 65,
                axis_fontSize: 12,
                tooltip_fontSize: 11,
                subchart_padding: 10,
                tooptip_width: 65,
            };
        }
    }

	render() {
		var { width, height, ratio } = this.props;
		var { data: initialData } = this.state;

        var margin = {left: 70, right: this.chartLayout.chartCanvas_margin_right, top:0, bottom: 0}
        
        var mainChartHeight = height + 30; // 30 is XAxis height
        
        const xScaleProvider = discontinuousTimeScaleProvider.inputDateAccessor(d => d.date);
        const {
			data: data,
			xScale: xScale,
			xAccessor: xAccessor,
			displayXAccessor: displayXAccessor,
		} = xScaleProvider(initialData);

        const start = xAccessor(data[data.length-1]);
		const end = xAccessor(data[0]);
		const xExtents = [start, end];
        
        const range = [data[Math.max(0, data.length - 150)], data[data.length-1]]

// <MouseCoordinateX
                        // rectWidth={MouseCoordinateX_rectWidth}
                        // at="bottom"
                        // orient="bottom"
                        // displayFormat={MouseCoordinateX_displayFormat} />
                        
        const listenerProps = {
            onMouseDown: this.handleMouseDown,
            onMouseUp: this.handleMouseUp,
            onMouseLeave: this.handleMouseLeave,
            onMouseMove: this.handleMouseMove,
        }

		return (
            <ChartCanvas ref={node => {this.canvas=node;}} width={width} height={mainChartHeight} pointsPerPxThreshold={2}
                ratio={ratio}
                margin={margin}
                seriesName="StockTimeLine"
                data={data}
                xScale={xScale}
                xAccessor={xAccessor}
                displayXAccessor={displayXAccessor}
                xExtents={xExtents}
                panEvent={false}
                zoomEvent={false}
                >
                <Chart id={1} height={height} padding={{ top: 0, bottom: 0 }}
                    yExtents={[d => [d.high, d.low]]}>
                    <XAxis axisAt="bottom" orient="bottom" ticks={5} fontSize={this.chartLayout.axis_fontSize} zoomEnabled={false}/>
                    <AreaSeries yAccessor={(d) => d.close}/>;
                    <MovingWindow ref={node => {this.movingWindow=node;}} height={height} range={range} usingProps={listenerProps} />
                </Chart>
            </ChartCanvas>
		);
	}
    
    handleMouseDown(e)
    {
        if (this.startDrag)
            return;
        
        const splitterWidth = 3;
        
        const { xAccessor: timeline_xAccessor, xScale: timeline_xScale, data } = this.canvas.props;
        const [ x, y ] = mousePosition(e);
        const { left: leftX } = getClosestItemIndexes(data, timeline_xScale.invert(x - splitterWidth), timeline_xAccessor);
        const { right: rightX } = getClosestItemIndexes(data, timeline_xScale.invert(x + splitterWidth), timeline_xAccessor);
        
        const { xAccessor, plotData } = this.props.baseref.mainChart.canvas.state;
        const firstPoint = xAccessor(first(plotData));
        const lastPoint = xAccessor(last(plotData));
        if (rightX < firstPoint || leftX > lastPoint) // Outside the movingWindow
            return;
        
        if (leftX < firstPoint) // Within the range of splitter_start (left < first < right)
            this.dragMethod="start";
        else if (rightX > lastPoint) // Within the range of splitter_end (left < end < right)
            this.dragMethod="end";
        else
        {
            this.dragMethod="mid";
            this.dragOriginX=getClosestItemIndexes(data, timeline_xScale.invert(x), timeline_xAccessor).left;
            this.dragOriginDataStartEnd={firstPoint, lastPoint};
        }
        this.startDrag = true;
    }
    
    handleMouseUp(e)
    {
        this.startDrag = false;
    }
    
    handleMouseLeave(e)
    {
        this.startDrag = false;
    }
    
    handleMouseMove(e)
    {
        if (this.startDrag)
        {
            const { xAccessor: timeline_xAccessor, xScale: timeline_xScale, data } = this.canvas.props;
            const [ x, y ] = mousePosition(e);
            const { left, right } = getClosestItemIndexes(data, timeline_xScale.invert(x), timeline_xAccessor);
            
            const { xAccessor, plotData } = this.props.baseref.mainChart.canvas.state;
            var newPointBound;
            if (this.dragMethod === "start")
                newPointBound = [data[left], last(plotData)];
            else if (this.dragMethod === "end")
                newPointBound = [first(plotData), data[right]];
            else
            {
                const diff = this.dragOriginX - left;
                const firstItem = Math.max(0, this.dragOriginDataStartEnd.firstPoint - diff);
                const lastItem = Math.min(data.length - 1, this.dragOriginDataStartEnd.lastPoint - diff);
                newPointBound = [data[firstItem], data[lastItem]];
            }
            
            if (this.dragOriginDataStartEnd.firstPoint == newPointBound[0] && this.dragOriginDataStartEnd.lastPoint == newPointBound[1])
                return; // No change

            const newXExtents = [xAccessor(newPointBound[0]), xAccessor(newPointBound[1])]
            this.props.baseref.mainChart.xAxisZoom(newXExtents);
            this.refreshMovingWindow(newPointBound[0], newPointBound[1]);
        }
    }
    
    refreshMovingWindow(firstPoint, lastPoint)
    {
        const window_range = [firstPoint, lastPoint]
        this.movingWindow.setState({
           range: window_range
        });
    }

    setData(data) {
        this.setState({
            data: data,
        })
    }
}

TTLTimeLineChart.propTypes = {
	data: PropTypes.array.isRequired,
	width: PropTypes.number.isRequired,
	height: PropTypes.number.isRequired,
	ratio: PropTypes.number.isRequired,
};

TTLTimeLineChart.defaultProps = {
};

export default TTLTimeLineChart


