/*global define*/
"use strict";

// react element
import React from "react";
import PropTypes from 'prop-types'; // ES6

// react-stockcharts
import { first, mousePosition, functor, getClosestItemIndexes } from "react-stockcharts/lib/utils";
import GenericChartComponent from "react-stockcharts/lib/GenericChartComponent";

class MovingWindow extends React.Component
{
    constructor(props)
    {
		super(props);
        
        this.renderSVG = this.renderSVG.bind(this);
        
        const {range, height, splitterWidth} = this.props
        
        this.state = {
            range: range,
        }
        this.midPath = movingWindowPath(height, splitterWidth);
        this.startPath = splitterPath(height, splitterWidth, true);
        this.endPath = splitterPath(height, splitterWidth, false);
    }
    
    render()
    {
        return <GenericChartComponent
			svgDraw={this.renderSVG}
			drawOn={[""]}
		/>;
    }
    
    renderSVG(moreProps)
    {
		const { xAccessor, xScale, width } = moreProps;
        const { className, stroke, fillWindow, fillSplitter, usingProps, height } = this.props;
        
        const { range } = this.state
        const [ startPoint, endPoint ] = range;
        
        const sx = xScale(xAccessor(startPoint));
        const ex = xScale(xAccessor(endPoint));
        
        // rect is used for listener added in usingProps
        return (
        <g className={className} {...usingProps}>
			<path key={"mw_mid"} d={this.midPath(sx, ex)} stroke={stroke} fill={fillWindow} opacity={1} />
			<path key={"mw_start"} d={this.startPath(sx)} stroke={stroke} fill={fillSplitter} opacity={0.8} />
			<path key={"mw_end"} d={this.endPath(ex)} stroke={stroke} fill={fillSplitter} opacity={0.8} />
            <rect width={width} height={height} style={{opacity: 0}} />
		</g>);
	}
}

function movingWindowPath(height, splitterWidth)
{
    return function (sx, ex)
    {
        return `M${sx+splitterWidth-1} ${0} `
            + `L${sx+splitterWidth-1} ${height} `
            + `L${ex-splitterWidth+1} ${height} `
            + `L${ex-splitterWidth+1} ${0} `
            + "Z";
    }
}

function splitterPath(height, splitterWidth, isStart)
{
    if (isStart)
    {
        return x => `M${x} ${0} `
            + `L${x} ${height} `
            + `L${x + splitterWidth} ${height} `
            + `L${x + splitterWidth} ${0} `
            + "Z";
    }
    else
    {
        return x => `M${x - splitterWidth} ${0} `
            + `L${x - splitterWidth} ${height} `
            + `L${x} ${height} `
            + `L${x} ${0} `
            + "Z";
    }
}

MovingWindow.propTypes =
{
	className: PropTypes.string,
	stroke: PropTypes.string,
    fillWindow: PropTypes.string,
    fillSplitter: PropTypes.string,
    splitterWidth: PropTypes.number,
    height: PropTypes.number,
    usingProps: PropTypes.object,
};

MovingWindow.defaultProps =
{
    // className: "react-stockcharts-enable-interaction react-stockcharts-svgpathannotation", // class name copy from Annotate, SvgPathAnnotation
    className: "react-stockcharts-enable-interaction react-stockcharts-annotate react-stockcharts-default-cursor", // class name copy from Annotate, SvgPathAnnotation
    fillWindow: "rgba(0,0,0,0.3)",
    fillSplitter: "#505050",
    splitterWidth: 3,
}

export default MovingWindow



