/*global define*/
"use strict";

// react element
import React from "react";
import PropTypes from 'prop-types'; // ES6

// D3 format
import { format } from "d3-format";
import { timeFormat, timeParse } from "d3-time-format";

// react-stockcharts
import { ChartCanvas, Chart } from "react-stockcharts";
import { discontinuousTimeScaleProvider } from "react-stockcharts/lib/scale";
import { CrossHairCursor, MouseCoordinateX, MouseCoordinateY, CurrentCoordinate } from "react-stockcharts/lib/coordinates";
import { XAxis, YAxis } from "react-stockcharts/lib/axes";
import { CandlestickSeries, LineSeries, AreaSeries, OHLCSeries, BarSeries, MACDSeries, RSISeries, StochasticSeries, SARSeries, BollingerSeries} from "react-stockcharts/lib/series";
import { OHLCTooltip, MovingAverageTooltip, BollingerBandTooltip, MACDTooltip, RSITooltip, StochasticTooltip, SingleValueTooltip } from "react-stockcharts/lib/tooltip";
import { sma, wma, ema, sar, bollingerBand, macd, rsi, stochasticOscillator } from "react-stockcharts/lib/indicator";

var priceFormat = format(".3f");

const bbAppearance = {
    stroke: {
        top: "#964B00",
        middle: "#FF6600",
        bottom: "#964B00",
    },
    fill: "#4682B4"
};
const stoAppearance = {
	stroke: Object.assign({},
		StochasticSeries.defaultProps.stroke)
};
const macdAppearance = {
	stroke: {
		macd: "#FF0000",
		signal: "#00F300",
	},
	fill: {
		divergence: "#4682B4"
	},
};
const upColor = "#CC0000";
const downColor = "#1054A9";

class TTLMainChart extends React.Component {
	constructor(props) {
		super(props);
        
        this.state = {
            data: this.props.data,
            mainChartSeries: createMainChartSeries("Candle"),
            subChartList: [],
            inChartList: [],
            userDrawing: [],
        }
        
		this.setData = this.setData.bind(this);
		this.setChartType = this.setChartType.bind(this);
		this.setMainChartSeries = this.setMainChartSeries.bind(this);
		this.setSubCharts = this.setSubCharts.bind(this);
		this.xAxisZoom = this.xAxisZoom.bind(this);
		this.onDrawComplete = this.onDrawComplete.bind(this);
        
        var isMob = false;
        if (isMob)
        {
            this.chartLayout = {
                chartCanvas_margin_right: 80,
                axis_fontSize: 22,
                tooltip_fontSize: 16,
                subchart_padding: 30,
                tooptip_width: 90,
            };
        }
        else
        {
            this.chartLayout = {
                chartCanvas_margin_right: 65,
                axis_fontSize: 12,
                tooltip_fontSize: 11,
                subchart_padding: 10,
                tooptip_width: 65,
            };
        }
    }
    
    componentDidMount() {
        this.canvas.subscribe("MainChart-1", { listener: this.props.onChartEvent });
    }

    componentWillUnmount() {
        this.canvas.unsubscribe("MainChart-1");
    }

	render() {
		var { width, height, ratio } = this.props;
		var { data: initialData, mainChartSeries, subChartList, chartType, inChartList, refreshState, xExtents } = this.state;

        if (!subChartList) subChartList = [];
        if (!inChartList) inChartList = [];
        
        var inChartCalculator=[];
        var indicatorCalculator=[];
        
        var margin = {left: 70, right: this.chartLayout.chartCanvas_margin_right, top:10, bottom: 30}
        
        var mainChartHeight = height + 40; //+ margin.top + margin.bottom;
        var gridHeight = height - margin.top - margin.bottom;
		var gridWidth = width - margin.left - margin.right;
        var yGrid = { innerTickSize: -1 * gridWidth, tickStrokeOpacity: 0.2 };
		var xGrid = { innerTickSize: -1 * gridHeight, tickStrokeOpacity: 0.2 };
        
        if (subChartList)
        {
            subChartList.forEach((chart) => {
                if (chart.props.exHeight) mainChartHeight+=chart.props.height; // exHeight==true is to exculde the sub-chart height from main chart
                if (chart.props.calculator) indicatorCalculator.push(chart.props.calculator);
            });
        }
        if (inChartList)
        {
            inChartList.forEach((chart) => {
                if (chart.props.calculator) inChartCalculator.push(chart.props.calculator);
            });
        }
        
        var MouseCoordinateX_rectWidth;
        var MouseCoordinateX_displayFormat;
        
        if (chartType == "IntraDay")
        {
            MouseCoordinateX_rectWidth = 90;
            MouseCoordinateX_displayFormat = timeFormat("%m-%d %H:%M:%S");
        }
        else if (chartType == "Monthly")
        {
            MouseCoordinateX_rectWidth = 70;
            MouseCoordinateX_displayFormat = timeFormat("%Y-%m");
        }
        else//if (chartType == "Daily")
        {
            MouseCoordinateX_rectWidth = 80;
            MouseCoordinateX_displayFormat = timeFormat("%Y-%m-%d");
        }
        
        var chartData = initialData;
        const yExtents = [d => [d.high, d.low]]
        for (let cal of inChartCalculator)
        {
            chartData = cal(chartData);
            yExtents.push(cal.accessor());
        }
        for (let cal of indicatorCalculator)
        {
            chartData = cal(chartData);
        }
        
        const xScaleProvider = discontinuousTimeScaleProvider.inputDateAccessor(d => d.date);
		const {
			data,
			xScale,
			xAccessor,
			displayXAccessor,
		} = xScaleProvider(chartData);
        
        if (!xExtents)
        {
            const start = xAccessor(data[data.length-1]);
            const end = xAccessor(data[Math.max(0, data.length - 150)]);
            xExtents = [start, end];
        }
        
        // var xScale = d3.time.scale();
//tickFormat={timeFormat("%H:%M:%S")}
// tickFormat={priceFormat}

		return (
            <ChartCanvas ref={node => {this.canvas=node;}} width={width} height={mainChartHeight} pointsPerPxThreshold={2}
                ratio={ratio}
                margin={margin}
                seriesName="Stock"
                data={data}
                xScale={xScale}
                xAccessor={xAccessor}
                displayXAccessor={displayXAccessor}
                xExtents={xExtents}
                clamp={true}
                >
                <Chart id={1} height={height} padding={{ top: 10, bottom: 20 }}
                    yExtents={yExtents}>
                    <XAxis axisAt="bottom" orient="bottom" ticks={8} {...xGrid} fontSize={this.chartLayout.axis_fontSize} zoomEnabled={false}/>
                    <YAxis axisAt="right" orient="right" ticks={5} {...yGrid} fontSize={this.chartLayout.axis_fontSize} zoomEnabled={false}/>
    
                    <MouseCoordinateX
                        rectWidth={MouseCoordinateX_rectWidth}
                        at="bottom"
                        orient="bottom"
                        displayFormat={MouseCoordinateX_displayFormat} />
                    <MouseCoordinateY
                        at="right"
                        orient="right"
                        displayFormat={format(".2f")} />
                    {mainChartSeries}
                    <OHLCTooltip xDisplayFormat={MouseCoordinateX_displayFormat} fontSize={this.chartLayout.tooltip_fontSize} origin={[-40, 0]}/>
                    {inChartList}
                </Chart>
                {subChartList}
                
                {/*<TrendLine
					ref={(node) => this.interact.trendLine = node}
					enabled={this.state.enableTrendLine}
					type="RAY"
					snap={true}
					snapTo={d => [d.high, d.low]}
					onComplete={this.onDrawComplete(0)}
					trends={userDrawing[0]}
                />*/}
                
                <CrossHairCursor />
            </ChartCanvas>
		);
	}

    setData(data) {
        this.setState({
            data: data,
        })
    }
    
    setChartType(pChartType)
    {
        this.setState({
           chartType: pChartType
        });
    }
    
    setMainChartSeries(pSeriesName)
    {
        var lvMainChartSeries = createMainChartSeries(pSeriesName);
        this.setState({
           mainChartSeries: lvMainChartSeries
        });
    }

    setSubCharts(pDisplayInChartVol, pInChartVarArray, pSubChartVarArray) {
        var lvInChartList = createInChartList(pInChartVarArray, this.chartLayout);
        var lvSubChartList = createSubChartList(pDisplayInChartVol, pSubChartVarArray, this.chartLayout);
        var lvData;
        if (this.state.data)
            lvData = this.state.data.map(function (r, i) {
                return clearDataPoint(r);
            });
        this.setState({
            data: lvData,
            inChartList: lvInChartList,
            subChartList: lvSubChartList
        });
    }
    
    xAxisZoom(newDomain)
    {
        this.canvas.xAxisZoom(newDomain);
    }
    
    onDrawComplete(id)
    {
        // return (trends) => 
        // {
            // this.setState({
                // enableDrawing: false,
                // userDrawing: [id] = trends
            // });
        // }
    }
}

TTLMainChart.fitWidth = function(pChart)
{
    return fitWidth(pChart);
}

var parseDate = timeParse("%Y-%m-%d %H:%M:%S");
function convertToDataPoint(pData, pPrevClose)
{
    var d = {};
	d.date = new Date(parseDate(pData[0]).getTime());
    d.close = pData[1];
	d.high = pData[2];
	d.low = pData[3];
	d.open = pData[4];
	d.volume = pData[5];
    if (!pPrevClose)
        d.pclose = pPrevClose;
    return d;
}

// ---------------------------------- helper method -------------------------------------
function clearDataPoint(pData)
{
    var d = {};
	d.date = pData.date;
    d.close = pData.close;
	d.high = pData.high;
	d.low = pData.low;
	d.open = pData.open;
	d.volume = pData.volume;
    d.pclose = pData.pclose;
    return d;
}

// -------------------------------- sub chart method ------------------------------------
// InChart
// Vol
// SMA  [Period]
// WMA  [Period]
// EMA  [Period]
// SAR  [Acc. Factor, Maximum]
// BB

// SubChart
// MACD [ShortEMA, LongEMA, SignalLine]
// RSI  [Period]
// STO  [K, D, KW] fast->KW=1, slow->KW=3

function createInChartList(pInChartVarArray, pChartLayout)
{
    var inChartList = [];
    var lvToolTipStruct = {};
    
    if (pInChartVarArray != undefined)
    {
        for (var i = pInChartVarArray.length-1; i >= 0; i--)
        {
            var [pChartName, pPara] = pInChartVarArray[i];
            var lvDataID = 100 + i; // start with 100
    
            prepareInChart(pChartName, lvDataID, lvToolTipStruct, pPara);
        }
        for (var i = pInChartVarArray.length-1; i >= 0; i--)
        {
            var [pChartName, pPara] = pInChartVarArray[i];
            var lvDataID = 100 + i; // start with 100
    
            var subChart = createInChart(pChartName, lvDataID, lvToolTipStruct, pPara, pChartLayout);
            inChartList = inChartList.concat(subChart);
        }
    }
    return inChartList;
}

function createSubChartList(pDisplayInChartVol, pSubChartVarArray, pChartLayout)
{
    var subChartList = [];
    var lvOriginFunc = (t) => { return (w, h) => [0, h-t] }
    if (pSubChartVarArray != undefined)
    {
        var lvTotalSubChartHeight = 0;
        for (var i = pSubChartVarArray.length-1; i >= 0; i--)
        {
            var [pChartName, pPara, pHeight] = pSubChartVarArray[i];
            if (pChartName == "InChartVol")
                continue; // skip
            var lvChartID = i + 10; // start with 10
            var lvAddPadding = (lvChartID == 10);
            pHeight += lvAddPadding ? 20 : 0;
    
            lvTotalSubChartHeight += pHeight;
            var lvOrigin = lvOriginFunc(lvTotalSubChartHeight);
            var subChart = createSubChart(pChartName, lvChartID, pHeight, lvOrigin, pPara, lvAddPadding, pChartLayout);
            subChartList.push(subChart);
        }
        if (pDisplayInChartVol)
        {
            var [pChartName, pPara, pHeight] = ["InChartVol", [], 150];
            var lvChartID = 3; // always use 3
            lvTotalSubChartHeight += pHeight;
            var lvOrigin = lvOriginFunc(lvTotalSubChartHeight);
            var subChart = createSubChart(pChartName, lvChartID, pHeight, lvOrigin, pPara, false, pChartLayout);
            subChartList.push(subChart);
        }
        subChartList.reverse();
    }
    return subChartList;
}

// Prepare layout before create in chart
function prepareInChart(pChartName, pDataID, pToolTipStruct, pPara)
{
    if (!pToolTipStruct.row0) // init
    {
        pToolTipStruct.row0 = {x:-18, y:15};
        pToolTipStruct.row1 = {x:-20, y:20};
    }

    if (pChartName == "InChartSMA")
    {
        if (pToolTipStruct.row1.y == 20)
            pToolTipStruct.row1 = {x:-18, y:60};
    }
    else if (pChartName == "InChartEMA")
    {
        if (pToolTipStruct.row1.y == 20)
            pToolTipStruct.row1 = {x:-18, y:60};
    }
    else if (pChartName == "InChartWMA")
    {
        if (pToolTipStruct.row1.y == 20)
            pToolTipStruct.row1 = {x:-18, y:60};
    }
    else if (pChartName == "InChartSAR")
    {
    }
    else if (pChartName == "InChartBB")
    {
    }
}

function createMainChartSeries(pSeriesName)
{
    if (pSeriesName == "Line")
    {
        return <LineSeries yAccessor={d => d.close} strokeWidth={2}/>;
    }
    if (pSeriesName == "Area")
    {
        return <AreaSeries yAccessor={d => d.close}/>;
    }
    else if (pSeriesName == "OHLC")
    {
        return <OHLCSeries />;
    }
    else // if (pSeriesName == "Candlestick") //or "Candle"
    {
        var candlechart = <CandlestickSeries 
            yAccessor = {d => ({ open: d.open, high: d.high, low: d.low, close: d.close, pclose: d.pclose })}
            stroke={d => d.pclose > d.close ? upColor : downColor}
            wickStroke={d => d.pclose > d.close ? upColor : downColor}
            fill={d => d.close > d.open ? "rgba(0,0,0,0)": (d.pclose > d.close ? upColor : downColor)}
            opacity={1}
            candleStrokeWidth={1}
            />;
        return candlechart;
    }
}

function createInChart(pChartName, pDataID, pToolTipStruct, pPara, pChartLayout)
{
    if (pChartName == "InChartSMA")
        return getInChartSMA(pDataID, pToolTipStruct, pPara, pChartLayout);
    else if (pChartName == "InChartWMA")
        return getInChartWMA(pDataID, pToolTipStruct, pPara, pChartLayout);
    else if (pChartName == "InChartEMA")
        return getInChartEMA(pDataID, pToolTipStruct, pPara, pChartLayout);
    else if (pChartName == "InChartSAR")
        return getInChartSAR(pDataID, pToolTipStruct, pPara, pChartLayout);
    else if (pChartName == "InChartBB")
        return getInChartBB(pDataID, pToolTipStruct, pPara, pChartLayout);
}

function createSubChart(pChartName, pNextChartID, pHeight, pOrigin, pPara, pAddPadding, pChartLayout)
{
    if (pChartName == "InChartVol")
        return getInChartVol(pNextChartID, pHeight, pOrigin, pPara, pChartLayout);
    else if (pChartName == "MACD")
        return getMACD(pNextChartID, pHeight, pOrigin, pPara, pAddPadding, pChartLayout);
    else if (pChartName == "RSI")
        return getRSI(pNextChartID, pHeight, pOrigin, pPara, pAddPadding, pChartLayout);
    else if (pChartName == "STO")
        return getSTO(pNextChartID, pHeight, pOrigin, pPara, pAddPadding, pChartLayout);
}

function getInChartWMA(pID, pToolTipStruct, pPara, pChartLayout)
{
    var [pPeriod] = pPara; // 10
    var lvInChartArray = [];
    var lvCal = [];

    var lvPeriodID = 0;
    var tooltipOptions = [];
    pPara.forEach((pPeriod) =>
    {
        lvPeriodID++;
        var calculator = wma()
            .id(pID+'P'+lvPeriodID+'A')
            .options({windowSize: pPeriod})
            .merge(((t) => (d, c) => {d[pID+'P'+t] = c})(lvPeriodID))
            .accessor(((t) => d => d[pID+'P'+t])(lvPeriodID));
        lvInChartArray.push(<LineSeries key={pID+'P'+lvPeriodID+'A'} calculator={calculator} yAccessor={calculator.accessor()} stroke={calculator.stroke()}/>);
        // lvInChartArray.push(<CurrentCoordinate key={pID+'P'+lvPeriodID+'B'} yAccessor={calculator.accessor()} fill={calculator.stroke()} />);
        lvCal.push(calculator);
        tooltipOptions.push({yAccessor: calculator.accessor(), type: calculator.type(), stroke: calculator.stroke(), windowSize: calculator.options().windowSize});
    })

    lvInChartArray.push(<MovingAverageTooltip key={pID+'C'} origin={[pToolTipStruct.row0.x, pToolTipStruct.row0.y]} width={pChartLayout.tooptip_width} fontSize={pChartLayout.tooltip_fontSize} options={tooltipOptions}/>);
    pPara.forEach((pPeriod) => pToolTipStruct.row0.x += pChartLayout.tooptip_width);
    return lvInChartArray;
}

