function getInChartSAR(pID, pToolTipStruct, pPara, pChartLayout)
{
    var [pSccelerationFactor, pMaxAccelerationFactor] = pPara; // 0.02,0.2
    var lvInChartArray = [];
    var lvCal = [];
    
    var lvPeriodID = 0;
    var calculator = sar()
        .options({accelerationFactor: pSccelerationFactor, maxAccelerationFactor: pMaxAccelerationFactor})
		.merge((d, c) => {d[pID] = c})
		.accessor(d => d[pID]);
    lvInChartArray.push(<SARSeries key={pID+'SA'} calculator={calculator} yAccessor={calculator.accessor()}/>);
    lvInChartArray.push(<SingleValueTooltip key={pID+'SB'} origin={[pToolTipStruct.row1.x, pToolTipStruct.row1.y]} fontSize={pChartLayout.tooltip_fontSize} calculator={calculator}
                        yLabel={'SAR (' + pSccelerationFactor + ', ' + pMaxAccelerationFactor + ')'} yAccessor={calculator.accessor()}
                        />);
    pToolTipStruct.row1.y += 15;
    return lvInChartArray;
}

function getInChartBB(pID, pToolTipStruct, pPara, pChartLayout)
{
    // var  = pPara; // no para
    var lvInChartArray = [];
    var calculator = bollingerBand()
		.merge((d, c) => {d[pID] = c})
		.accessor(d => d[pID]);
    lvInChartArray.push(<BollingerSeries key={pID+'BA'} yAccessor={calculator.accessor()} calculator={calculator} {...bbAppearance}/>);
    lvInChartArray.push(<BollingerBandTooltip key={pID+'BB'} origin={[pToolTipStruct.row1.x, pToolTipStruct.row1.y]} fontSize={pChartLayout.tooltip_fontSize} yAccessor={calculator.accessor()} options={calculator.options()}/>);
    pToolTipStruct.row1.y += 15;
    return lvInChartArray;
}

function getInChartVol(pID, pHeight, pOrigin, pPara, pChartLayout)
{
    return (<Chart id={pID} key={pID}
        yExtents={d => d.volume}
        height={pHeight} origin={pOrigin}>
        <YAxis axisAt="left" orient="left" fontSize={pChartLayout.axis_fontSize} ticks={5} tickFormat={format(".0s")}/>
    
        <MouseCoordinateY
            at="left"
            orient="left"
            displayFormat={format(".4s")} />
    
        <BarSeries yAccessor={d => d.volume} />
    </Chart>)
}

function getRSI(pID, pHeight, pOrigin, pPara, pAddPadding, pChartLayout)
{
    var [pPeriod] = pPara; //14
    var topPadding = pChartLayout.subchart_padding + (pAddPadding ? 20 : 0);
    
    var calculator = rsi()
        .options({windowSize: pPeriod})
        .merge((d, c) => {d[pID] = c})
        .accessor(d => d[pID]);
    return (
        <Chart
            id={pID}
            key={pID}
            calculator={calculator}
            height={pHeight}
            exHeight={true}
            yExtents={[0, 100]}
            origin={pOrigin}
            padding={{ top: topPadding, bottom: 10 }}>
            
            <XAxis axisAt="bottom" orient="bottom" showTicks={false} outerTickSize={0}/>
            <YAxis axisAt="right" orient="right" fontSize={pChartLayout.axis_fontSize} tickValues={[30, 50, 70]}/>
    
            <MouseCoordinateY
                at="right"
                orient="right"
                displayFormat={format(".2f")} />
    
            <RSISeries
                yAccessor={calculator.accessor()}
            />
            <RSITooltip
                origin={[-38, topPadding]}
                yAccessor={calculator.accessor()}
                options={calculator.options()}
                fontSize={pChartLayout.tooltip_fontSize}
            />
        </Chart>)
}

function getMACD(pID, pHeight, pOrigin, pPara, pAddPadding, pChartLayout)
{
    var [pShortEMA, pLongEMA, pSignalLine] = pPara;//12,26,9
    var topPadding = pChartLayout.subchart_padding + (pAddPadding ? 20 : 0);
    var calculator = macd()
        .options({fast: pShortEMA, slow: pLongEMA, signal: pSignalLine})
        .merge((d, c) => {d[pID] = c})
        .accessor(d => d[pID]);
    return (
        <Chart
            id={pID}
            key={pID}
            calculator={calculator}
            height={pHeight}
            exHeight={true}
            yExtents={calculator.accessor()}
            origin={pOrigin}
            padding={{ top: topPadding, bottom: 10 }}>
            
            <XAxis axisAt="bottom" orient="bottom" showTicks={false} outerTickSize={0}/>
            <YAxis axisAt="right" orient="right" fontSize={pChartLayout.axis_fontSize} ticks={5} />
    
            <MouseCoordinateY
                at="right"
                orient="right"
                displayFormat={format(".2f")} />
    
            <MACDSeries
                yAccessor={calculator.accessor()}
                {...macdAppearance}
                />
            <MACDTooltip
                origin={[-38, topPadding]}
                yAccessor={calculator.accessor()}
                options={calculator.options()}
                fontSize={pChartLayout.tooltip_fontSize}
                appearance={macdAppearance}
                />
        </Chart>)
}

function getSTO(pID, pHeight, pOrigin, pPara, pAddPadding, pChartLayout)
{
    var [pK, pD, pKW] = pPara; //slow:14,5,3  fast:20,5,1
    var topPadding = pChartLayout.subchart_padding + (pAddPadding ? 20 : 0);
    var calculator = stochasticOscillator()
        .options({windowSize: pK, kWindowSize: pKW, dWindowSize: pD})
        .merge((d, c) => {d[pID] = c})
        .accessor(d => d[pID]);
    return (
        <Chart
            id={pID}
            key={pID}
            calculator={calculator}
            height={pHeight}
            exHeight={true}
            yExtents={[0, 100]}
            origin={pOrigin}
            padding={{ top: topPadding, bottom: 10 }}>
            
            <XAxis axisAt="bottom" orient="bottom" showTicks={false} outerTickSize={0}/>
            <YAxis axisAt="right" orient="right" fontSize={pChartLayout.axis_fontSize} tickValues={[20, 50, 80]}/>
        
            <MouseCoordinateY
                at="right"
                orient="right"
                displayFormat={format(".2f")} />
        
            <StochasticSeries
                yAccessor={calculator.accessor()}
                {...stoAppearance}
                />
            <StochasticTooltip
                origin={[-38, topPadding]}
                yAccessor={calculator.accessor()}
                options={calculator.options()}
                appearance={stoAppearance}
                fontSize={pChartLayout.tooltip_fontSize} 
                label="STO"
                />
        </Chart>)
}

TTLMainChart.propTypes = {
	data: PropTypes.array.isRequired,
	width: PropTypes.number.isRequired,
	height: PropTypes.number.isRequired,
	ratio: PropTypes.number.isRequired,
};

TTLMainChart.defaultProps = {
};

TTLMainChart.convertToDataPoint = convertToDataPoint;

export default TTLMainChart


