/*global define*/
"use strict";

import "babel-polyfill";
import "canvas-5-polyfill"; // For IE

// requestAnimationFrame polyfill
import requestFrame from 'request-frame';
requestFrame('native');

// react element
import React from "react";
import PropTypes from 'prop-types'; // ES6

// react-stockcharts
import { ChartCanvas, Chart } from "react-stockcharts";
import { fitWidth } from "react-stockcharts/lib/helper";
import { discontinuousTimeScaleProvider } from "react-stockcharts/lib/scale";
import { CrossHairCursor, MouseCoordinateX, MouseCoordinateY, CurrentCoordinate } from "react-stockcharts/lib/coordinates";
import { XAxis, YAxis } from "react-stockcharts/lib/axes";
import { CandlestickSeries, LineSeries, AreaSeries, OHLCSeries, BarSeries, MACDSeries, RSISeries, StochasticSeries, SARSeries, BollingerSeries} from "react-stockcharts/lib/series";
import { OHLCTooltip, MovingAverageTooltip, BollingerBandTooltip, MACDTooltip, RSITooltip, StochasticTooltip, SingleValueTooltip } from "react-stockcharts/lib/tooltip";
import { sma, wma, ema, sar, bollingerBand, macd, rsi, stochasticOscillator } from "react-stockcharts/lib/indicator";
import { TrendLine, FibonacciRetracement, EquidistantChannel, StandardDeviationChannel, GannFan } from "react-stockcharts/lib/interactive";
import { first, last } from "react-stockcharts/lib/utils";

// TTL element
import TTLMainChart from "./TTLMainChart"
import TTLTimeLineChart from "./TTLTimeLineChart"

class TTLChart extends React.Component {
	constructor(props) {
		super(props);
        
        this.state = {
            data: dataAddPClose(this.props.data)
        }
		this.refresh = this.refresh.bind(this);
		this.addData = this.addData.bind(this);
		this.setData = this.setData.bind(this);
		this.setChartType = this.setChartType.bind(this);
		this.setMainChartSeries = this.setMainChartSeries.bind(this);
		this.setSubCharts = this.setSubCharts.bind(this);
		this.handleMainChartEvents = this.handleMainChartEvents.bind(this);
        
        var isMob = false;
        if (isMob)
        {
            this.chartLayout = {
                chartCanvas_margin_right: 80,
                axis_fontSize: 22,
                tooltip_fontSize: 16,
                subchart_padding: 30,
                tooptip_width: 90,
            };
        }
        else
        {
            this.chartLayout = {
                chartCanvas_margin_right: 65,
                axis_fontSize: 12,
                tooltip_fontSize: 11,
                subchart_padding: 10,
                tooptip_width: 65,
            };
        }
    }
    
    componentDidMount() {
        this.mainChart.canvas.subscribe("MainChart-1", { listener: this.handleMainChartEvents });
        // this.timeLine.subscribe("TimeLine-1", { listener: this.handleTimeLineEvents });
        // select(this.timeLine)
            // .on(MOUSEENTER, this.handleMouseEnter)
            // .on(MOUSELEAVE, this.handleMouseLeave);
    }

    componentWillUnmount() {
        this.mainChart.canvas.unsubscribe("MainChart-1");
        // this.timeLine.unsubscribe("TimeLine-1");
        // select(this.timeLine)
            // .on(MOUSEENTER, null)
            // .on(MOUSELEAVE, null);
    }

	render() {
		var { width, height, ratio } = this.props;
		var { refreshState, data } = this.state;

		return (
            <div>
                <TTLMainChart width={width} height={height} ratio={ratio} ref={node => {this.mainChart=node;}} baseref={this}
                    data={data}
                    onChartEvent={this.handleMainChartEvents}
                    />
                <TTLTimeLineChart width={width} height={100} ratio={ratio} ref={node => {this.timeLine=node;}} baseref={this}
                    data={data}
                    />
            </div>
		);
	}
    
    handleMainChartEvents(type, moreProps, state)
    {
        if (type === "pan" || type === "zoom")
            this.timeLine.refreshMovingWindow(first(moreProps.plotData), last(moreProps.plotData));
    }
    
    refresh()
    {
        this.setState({
            refreshState: !this.state.refreshState
        });
    }

    addData(data) {
        var lvNewData = this.state.data.slice();
        if (lvNewData.length > 0)
            lvNewData.push(convertToDataPoint(data, lvNewData[lvNewData.length-1].close));
        else
            lvNewData.push(convertToDataPoint(data));
        this.mainChart.setState({
            data: lvNewData,
        })
        this.timeLine.setState({
            data: lvNewData,
        })
    }

    setData(data) {
        var lvNewData = dataAddPClose(data);
        setState({
            data : lvNewData
        });
    }
    
    setChartType(pChartType)
    {
        this.mainChart.setChartType(pChartType);
    }
    
    setMainChartSeries(pSeriesName)
    {
        this.mainChart.setMainChartSeries(pSeriesName);
    }

    setSubCharts(pDisplayInChartVol, pInChartVarArray, pSubChartVarArray) {
        this.mainChart.setSubCharts(pDisplayInChartVol, pInChartVarArray, pSubChartVarArray)
    }
}

TTLChart.propTypes = {
	data: PropTypes.array.isRequired,
	width: PropTypes.number.isRequired,
	// height: PropTypes.number.isRequired,
	ratio: PropTypes.number.isRequired,
    data: PropTypes.array.isRequired,
};

TTLChart.defaultProps = {
};

TTLChart.convertToDataPoint = TTLMainChart.convertToDataPoint;

TTLChart.fitWidth = function(pChart)
{
    return fitWidth(pChart);
}

function dataAddPClose(pDataArray)
{
    var len = pDataArray.length;
    for (var i=1; i<len; ++i)
        pDataArray[i].pclose = pDataArray[i-1].close;
    return pDataArray;
}

export default TTLChart


