/*global define*/
"use strict";

// css
import "../css/bootstrap-iso.css";
// import "../css/bootstrap.css";
// import "../css/bootstrap-theme.css";
import "../css/react-bootstrap-switch.css";

// react element
import React from "react";
import PropTypes from 'prop-types'; // ES6

import { Form, Button, ButtonGroup, Checkbox, FormControl, FormGroup, ControlLabel, HelpBlock, Grid, Row, Col, InputGroup, Panel, ListGroup, ListGroupItem, Dropdown, MenuItem, DropdownButton, PanelGroup, Glyphicon, Popover, Overlay, OverlayTrigger } from 'react-bootstrap';
import Switch from "react-bootstrap-switch"

class TTLChartEditControl extends React.Component
{
    constructor(props) {
		super(props);
        // Bind function
		this.refresh = this.refresh.bind(this);
        // Initialize variable
        var { chartMethod } = this.props;
        
        this.chartMethodGlobal = chartMethod;
        
        this.refMap = {};
        this.state={
            refreshState: false,
            controlShow: false,
        };
    }
    
    refresh()
    {
        this.setState({
            refreshState: !this.state.refreshState
        });
    }
    
    render()
    {
        const { refreshState, controlShow } = this.state;
        const cm = ()=>{}
        return (
            <div>
                <Dropdown pullRight>
                    <Dropdown.Toggle noCaret bsSize='sm'>
                        <Glyphicon glyph="pencil" />
                    </Dropdown.Toggle>
                    <Dropdown.Menu>
                        <MenuItem eventKey="TrendLine">Trend Line</MenuItem>
                        <MenuItem eventKey="2">Another action</MenuItem>
                        <MenuItem eventKey="3" active>Active Item</MenuItem>
                        <MenuItem divider />
                        <MenuItem eventKey="4">Separated link</MenuItem>
                    </Dropdown.Menu>
                </Dropdown>
            </div>
        );
    }
    
    setChartMethod(pChartMethod)
    {
        this.chartMethodGlobal = pChartMethod;
    }
}

TTLChartEditControl.propTypes = {
	chartMethod: PropTypes.object.isRequired
};

TTLChartEditControl.defaultProps = {
};

export default TTLChartEditControl


